@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.calculator{display:flex;flex-direction:column;position:relative;height:500px;width:400px;background-color:black;border:3px solid #40424B}@media only screen and (max-height: 550px){.calculator{height:80vh}}@media only screen and (max-height: 400px){.calculator{height:80vh}}

@font-face{font-family:"Digital";src:url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot");src:url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix") format("embedded-opentype"),url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2") format("woff2"),url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff") format("woff"),url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf") format("truetype"),url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7") format("svg")}.screen{background-color:black;height:100px;color:white;font-family:"Digital";font-size:50px;display:flex;flex-direction:column;justify-content:center;align-items:flex-end;padding:10px 10px 5px 10px}@media only screen and (max-height: 550px){.screen{height:20%;font-size:30px}}.screen .history{height:30px;color:#ea9400;font-size:0.5em}.screen .main-screen{padding-top:5px}.screen .small{font-size:0.7em}

.button{background-color:#4D4D4D;color:white;font-size:25px;display:flex;align-items:center;justify-content:center;outline:1px solid black}.button.ac{background-color:#AC3939;grid-column-start:1;grid-column-end:3}.button.equal{background-color:#004466;grid-row:4 / span 2;grid-column:4;z-index:2}.button.operation{background-color:#666666}.button:nth-last-child(2){grid-column-start:1;grid-column-end:3}.button:hover{cursor:pointer;outline:1px solid grey;z-index:3}

.buttons-layout{height:400px;display:grid;grid-template-columns:repeat(4, 25%);grid-template-columns:repeat(4, auto)}@media only screen and (max-height: 550px){.buttons-layout{height:80%}}

.footer{margin-top:19px}.footer a{color:black;margin-left:10px}

body {
  background-color: #C2C2D6;
}

.app {
  font-family: 'Share Tech Mono', monospace;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 50px;
  text-align: center;
}
